import opg from '../../../ts/opg';
import PopUp from '../../../ts/ui/PopUp';
import { api } from '../../../ts/util/api';
import { array, request } from '../../../ts/util/utils';
import { store } from '../../../ts/util/store';
import { Languages } from '../../../ts/util/Languages';
import { ListBox } from '../../../ts/ui/FormControls';

api({
	product: 'product/GetProductGroup',
	region: 'country/GetAllContinent',
	segment: 'product/GetProductTypes',
	myList: 'buyer/GetBuyerChooseExhibitions',
	allList: 'exhibition/GetAllExhibtion',
	'search!POST': 'exhibition/SearchExhibitionByFilter',
	'sendScheduleMessage!post': 'message/SendScheduleMessage',
	likeExhibtionForBuyerByBarCodeInList: 'buyer/LikeExhibtionForBuyerByBarCodeInList',
	unlike: 'buyer/UnLikeExhibtionForBuyerByBarCode',
	'updateListSort!post': 'buyer/UpdateBuyerChooseExhibitions'
});

const currentLng = Languages.current;
const lngPkg = Languages.package;

const by = request['by'];
const scheduleAddtoTime = store.get('scheduleAddtoTime') == null ? '' : store.get('scheduleAddtoTime');
const fromSchedule = request['from'] === 'schedule';
const wishListStage = store.get('Stage') === 'wishlist' && store.get('RoleName') == 'buyer' && by != 'choose';

let selRegion : ListBox , selProduct : ListBox , selSegment : ListBox;
let selCreated = 0 ;

$.when(
	api.region(data => {
		selRegion = opg('#selRegion').listBox({
			data,
			name: 'region',
			autoPrependBlank: '----',
			text: `chau_${currentLng}`,
			value: `chau_${currentLng}`,
			onSelect: searchContents
		});
	}),
	
	api.product(data => {
		selProduct = opg('#selProduct').listBox({
			data,
			name: 'product',
			autoPrependBlank: '----',
			text: `group_${currentLng}`,
			value: `id`,
			onSelect: searchContents
		});
	}),
	
	api.segment(data => {
		selSegment = opg('#selSegment').listBox({
			data,
			name: 'segment',
			autoPrependBlank: '----',
			text: `group_${currentLng}`,
			value: `id`,
			onSelect: searchContents
		});
	})
).then(function(){
	selCreated = -1 ;
	switch (by) {
		case 'product':
			//api.product({product: request['regionId'],scheduleAddtoTime:scheduleAddtoTime}, data => showData(data));
			//$("#selProduct").val(request['regionId']);
			//
			selProduct.setValue(request['regionId']);
			searchContents();
			break;
		case 'choose':
			//api.choose({scheduleAddtoTime:scheduleAddtoTime},data => showData(data));
			$("#mytext2").html(lngPkg.tips5);
			break;
		case 'region':
			//api.region({continent: request['regionId'],scheduleAddtoTime:scheduleAddtoTime}, data => showData(data));
			//$("#selRegion").val(request['regionId']);
			//
			selRegion.setValue(request['regionId']);
			searchContents();
			break;
		case 'segment':
			//api.segment({group: request['productId'],scheduleAddtoTime:scheduleAddtoTime}, data => showData(data));
			//$("#selSegment").val(request['productId']);
			//
			selSegment.setValue(request['productId']);
			searchContents();
			break;
		case 'search':
			//api.search({keyword: request['keyword'],scheduleAddtoTime:scheduleAddtoTime}, data => showData(data));
			$("#iptKeyword").val(request['keyword']);
			searchContents();
			break;
		case 'all':
			resetContents();
			searchContents();
			//api.all({scheduleAddtoTime:scheduleAddtoTime},data => showData(data));
			break;
		default:
	}
});


let slippyList = document.querySelector('#slippyList');

slippyList.addEventListener(
	'slip:beforewait',
	function(e) {
		e.preventDefault();
	},
	false
);

slippyList.addEventListener(
	'slip:beforereorder',
	function(e) {
		if (/ico-remove/.test((e.target as HTMLElement).className)) {
			e.preventDefault();
			
			top.opg.confirm(
				lngPkg.Content,
				function() {
					api.unlike({ exhibitionBarCode: $(e.target).data('barcode') }, function() {
						showMyList();
					});
				},
				{
					title: lngPkg.Tips ,
				}
			);
			
		}
	},
	false
);

slippyList.addEventListener(
	'slip:reorder',
	function(e: any) {
		(e.target as HTMLElement).parentNode.insertBefore(e.target as HTMLElement, (e.detail as any).insertBefore);
		updateSort();
		return false;
	},
	false
);

new Slip(slippyList);

showMyList();

//api.search({"keyword":"","region":"","product":"","segment":""}, data => showData(data));

const pic = __uri('assets/bge.jpg');
const noShow = {
	barcode: 1,
	company_logo: 1,
	logourl: 1
};

const alpha = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
let list: any = {};

const divList = $('#divList');

divList.on('click', 'li', function(e) {
	let item = list[this.getAttribute('data-key')][this.getAttribute('data-index')];
	let ppic = filterRegistrationLogoUrl(item.company_logo);
	let ppic2 = filterRegistrationLogoUrl(item.logourl);
	let toBarCode = item.barcode;

	console.log(toBarCode, e.target);

	if (e.target.tagName === 'IMG') {
		addExhibition(toBarCode);
		return false;
	}

	let html = '';

	if (ppic != '' && ppic != undefined && ppic != null)
		html +=
			`<img onerror="src='../../pages/itb/exhibitors/assets/bge.jpg'" src="` +
			ppic +
			`" style="width: auto;height: 154px;display: block;margin: 20px auto;"><div style="padding:20px; height: 300px; overflow: auto">`;
	else if (ppic2 != '' && ppic2 != undefined && ppic2 != null)
		html +=
			`<img onerror="src='../../pages/itb/exhibitors/assets/bge.jpg'" src="` +
			ppic2 +
			`" style="width: auto;height: 154px;display: block;margin: 20px auto;"><div style="padding:20px; height: 300px; overflow: auto">`;
	else
		html += `<img src="../../pages/itb/exhibitors/assets/bge.jpg" style="width: 200px;height: 154px;display: block;margin: 20px auto;">
					<div style="padding:20px; height: 300px; overflow: auto">`;

	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;
                    align-items:center;"><img src="../../pages/itb/exhibitors/assets/booth_no.png" 
                    style="width:20px;height:20px;border:0px;"/>
                        &emsp;${lngPkg.booth_no}
                </p>`;
	html += `<p><a href="../itb/navigation/index.html?booth=${item['booth_no']}" 
                    target="_self"><img src="../../pages/itb/exhibitors/assets/fid.png"
                     style="width:20px;height:20px;border:0px;"/>
                        ${item['booth_no']}
                </a></p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;
                    align-items:center;"><img src="../../pages/itb/exhibitors/assets/country.png" 
                        style="width:20px;height:20px;border:0px;"/>
                            &emsp;${lngPkg.country}
                </p>`;
	html += `<p >${item['countryname']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;
                    align-items:center;"><img src="../../pages/itb/exhibitors/assets/company_describe.png" 
                    style="width:20px;height:20px;border:0px;"/>
                        &emsp;${lngPkg.company_describe}
                </p>`;
	html += `<p style="word-wrap:break-word;width:500px;">${item['company_msg']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/web.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.web
	}</p>`;
	html += `<p >${item['web']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/product.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.products
	}</p>`;
	html += `<p >${item['product']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/segments.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.segments
	}</p>`;
	html += `<p >${item['segments']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/regions.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.regions
	}</p>`;
	html += `<p >${item['region1']} ${item['region2']}</p>`;

	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/email.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.email
	}</p>`;
	html += `<p >${item['email']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/fax.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.fax
	}</p>`;
	html += `<p >${item['fax']}</p>`;
	html += `<p style="color:#3ca9e2;font-size:16px;display:flex;align-items:center;"><img src="../../pages/itb/exhibitors/assets/tel.png" style="width:20px;height:20px;border:0px;"/>&emsp;${
		lngPkg.tel
	}</p>`;
	html += `<p >${item['tel']}</p>`;

	html += '</div>';

	let topbutton = '',
		buttons = null;
	let height = 540;

	if (height === 200) {
		html = '<div style="height: 200px; overflow-y: auto">' + html + '</div>';
	} else {
		html = '<div>' + html + '</div>';
	}

	if (fromSchedule) {
		height = 200;

		if (!buttons) {
			buttons = {};
		}

		buttons['btn1'] = {
			text: lngPkg.dating,
			onClick: function() {
				api.sendScheduleMessage(
					{
						msgTitle: lngPkg.msgTitle,
						msgContent: request['info'].replace(/\+/gi, ' '),
						toBarCode
					},
					data => {
						pop.close();
						window.location.href = __uri('../schedule/index.html');
					}
				);
				return true;
			}
		};
		topbutton =
			`<button  style="float:right;margin-top:5px;margin-right:40px;" class="topbtn" data-index="1" data-id="` +
			toBarCode +
			`" >` +
			lngPkg.dating +
			`</button>`;
	}

	if (wishListStage && item['appointment_total'] != '0') {
		if (!buttons) {
			buttons = {};
		}

		buttons['btn2'] = {
			text: lngPkg.wishListLike,
			onClick: function() {
				addExhibition(toBarCode);
				return true;
			}
		};

		topbutton =
			`<button  style="float:right;margin-top:5px;margin-right:40px;" class="topbtn" data-index="2" data-id="` +
			toBarCode +
			`" >` +
			lngPkg.wishListLike +
			`</button>`;
	}

	let pop: PopUp = opg.popTop(html, {
		title: item.company.length > 30 ? item.company.substr(0, 30) + '...' + topbutton : item.company + topbutton,
		width: 600,
		height: height,
		buttons
	});

	pop.titleBar.on('click', '.topbtn', function() {
		let btn = $(this),
			toBarCode = btn.data('id'),
			index = btn.data('index');

		if (index == '1') {
			api.sendScheduleMessage(
				{
					msgTitle: lngPkg.msgTitle,
					msgContent: request['info'].replace(/\+/gi, ' '),
					toBarCode
				},
				data => {
					pop.close();
					window.location.href = __uri('../schedule/index.html');
				}
			);
			return true;
		} else if (index == '2') {
			pop.close();
			addExhibition(toBarCode);
			return true;
		}
	});
});

function showData(arr) {
	list = {};
	let data;
	if (Languages.current === 'en') {
		data = array.sort(arr, 'company', function(a, b) {
			let A = a['company'].toUpperCase(),
				B = b['company'].toUpperCase();
			return A === B ? 0 : A > B ? 1 : -1;
		});
		for (let i = 0, l = data.length; i < l; i++) {
			let it = data[i],
				f = it['company'].substr(0, 1).toUpperCase();
			if (!list[f]) {
				if (alpha.indexOf(f) === -1) f = '#';
				list[f] = [];
			}
			list[f].push(it);
		}
	} else {
		data = array.sort(arr, 'company', function(a, b) {
			let A = makePy(a['company'])[0].toUpperCase(),
				B = makePy(b['company'])[0].toUpperCase();
			return A === B ? 0 : A > B ? 1 : -1;
		});
		for (let i = 0, l = data.length; i < l; i++) {
			let it = data[i],
				f = makePy(it['company'])[0]
					.substr(0, 1)
					.toUpperCase();

			if (!list[f]) {
				if (alpha.indexOf(f) === -1) f = '#';
				list[f] = [];
			}
			list[f].push(it);
		}
	}

	divList.html('');

	for (let key in list) {
		divList.append(`<h1> ${key}</h1>`);
		let ul = $(`<ul class="${currentStyle === 'list' ? 'ulNewList' : 'ulList'}"></ul>`);
		ul.bindList({
			list: list[key],
			template:
				'<li data-index="${:index}" data-key="' +
				key +
				'"> ${appointment_total:=showIcon} ${selected:=selectedIcon}' +
				'<div class="imgHold sm${appointment_total}" style="background-image: url(${company_logo:=gLogo})"></div> ${company} </li>',
			itemRender: {
				
				gLogo: function(company_logo, i, row) {
					let x =
						filterRegistrationLogoUrl(company_logo) ||
						filterRegistrationLogoUrl(row.logourl) ||
						'assets/no-logo.png';
					return `'${x}'`;
				},
				showIcon: function(appointment_total, i, row) {
					let html = '';
					if (wishListStage && appointment_total != '0') {
						html += `<img src="./assets/pc02.png" class="ico-add" />`;
					}
					return html;
				},
				selectedIcon : function(selected){
					return  `<img src="./assets/ico-heart.png" class="icon-heart" style="visibility:${selected?'visible':'hidden'};" />`
				}
			}
		});
		divList.append(ul);
	}
}

const toggleViewStyle = document.querySelector('#toggleViewStyle');
let currentStyle = store.get('wishListViewStyle', 'list');
toggleViewStyle.className = currentStyle;

$('#toggleViewStyle').click(function() {
	currentStyle = currentStyle === 'thumb' ? 'list' : 'thumb';
	$(toggleViewStyle).toggleClass('list thumb');
	$('ul', divList).toggleClass('ulNewList ulList');
	store.set('wishListViewStyle', currentStyle);
});

function filterRegistrationLogoUrl(logourl) {
	if (logourl != null && logourl != undefined && $.trim(logourl) != 'N/A' && $.trim(logourl) != '未提供') {
		let newurl = logourl.split('/');
		return '/Logo/' + newurl[newurl.length - 1];
	} else return '';
}

function searchContents() {
	if (selCreated === -1) {
		let param = $('#divSearch').fieldsToJson();
		api.search(param, data => showData(data));
	}
}

function resetContents() {
	$("#selRegion option:first").prop("selected", 'selected'); 
	$("#selProduct option:first").prop("selected", 'selected'); 
	$("#selSegment option:first").prop("selected", 'selected'); 
	$("#iptKeyword").val("");
}


document.getElementById('btnSearch').addEventListener('click', searchContents, false);
document.getElementById('btnReset').addEventListener('click', resetContents, false);


function showMyList() {
	api.myList({ scheduleAddtoTime }, data => {
		$(slippyList).bindList({
			list: data,
			template:
				'<li data-barcode="${barcode}"><b class="ico-remove" data-barcode="${barcode}">-</b> [${:rowNum}] ${company} <b class="ico-handle">≡</b></li>'
		});
	});
}

function addExhibition(exhibitionBarCode) {
	api.likeExhibtionForBuyerByBarCodeInList({ exhibitionBarCode }, data => {
		showMyList();
		let popy = opg.popTop(
			$(
				`<div style="border-radius: 5px;background-color: #999;line-height: 100px; color:#ffffff;width:300px;height:100px;text-align: center;font-size:22px;">${
					lngPkg.addWishListOk
				}</div>`
			)
		);
		setTimeout(() => popy.close(), 1000);
	});
}

function updateSort() {
	let exhibitionBarCodes = [];
	$('li', slippyList).each((i, li: HTMLLIElement) => {
		exhibitionBarCodes.push(li.getAttribute('data-barcode'));
	});

	api.updateListSort({ exhibitionBarCodes }, showMyList);
}



